<template>
  <InfoComp :resetGame="resetGame" :numGuessesRemaining="numGuessesRemaining" :targetOp="targetOp" :solved="solved"/>
  <div>
    <div class="list-head">
      <input type="text" v-model="searchText" @input="filterOps" placeholder="Enter Opterator Guess Here">
      <button @click="guessOp(searchText)">Guess!</button>
    </div>

    <div class="list-body">
      <ul v-if="showUL == true">
        <li v-for="op in filteredOps" :key="op" @click="selectOp(op.Name)">
          <img :src="op.Icon" class="op-image">
          <span class="op-name">{{ op.Name }}</span>
        </li>
      </ul>
    </div>

    <OperatorAnswerComp :opGuesses="opGuesses" :targetOp="targetOp" />
  </div>
</template>

<script>
import operatorData from '@/assets/Op_Data.json';
import OperatorAnswerComp from './OperatorAnswerComp.vue';
import InfoComp from './InfoComp.vue';
import axios from 'axios';

export default {
  props: {
    showSearchCompUL: Function,
    incrementHS: Function,
    resetHS: Function,
    showUL: Boolean
  },

  components: {
    OperatorAnswerComp,
    InfoComp
  },

  data() {
    return {
      opData: operatorData,
      targetOp: [],
      searchText: "",
      filteredOps: [],
      opGuesses: [],
      numGuessesRemaining: 6,
      solved: false
    };
  },

  mounted() {
    this.genTargetOp();

    axios.get('http://localhost:3000/')
    .then(response => {
      console.log(response.data);
    })
    .catch(error => {
      console.log(error.message);
    });
  },

  watch: {
    numGuessesRemaining: {
      handler(newVal){
        if(newVal == 0){
          this.resetHS();
        }
      }
    }
  },

  methods: {
    filterOps() {
      this.filteredOps = this.opData
      .filter(op => op.Name.toLowerCase().includes(this.searchText.toLowerCase()));

      this.filteredOps.length > 0 ? this.showSearchCompUL(true) : this.showSearchCompUL(false);
    },


    selectOp(option) {
      this.searchText = option;
      this.filteredOps = [];
    },

    guessOp(option) {
      let validGuess = false;

      if (this.numGuessesRemaining == 0 || this.solved) {
        return;
      }
      if(this.targetOp.Name == option){
        this.solved = true;
        this.incrementHS();
      }

      for (var i = 0; i < this.opData.length; i++) {
        if (this.opData[i].Name == option) {
          this.opGuesses.push(operatorData[i]);
          validGuess = true
        }
      }

      if(validGuess){
        this.numGuessesRemaining -= 1;
        this.searchText = "";
      }
    },

    genTargetOp() {
      let len = this.opData.length;
      let randomIndex = Math.floor(Math.random() * len);

      this.targetOp = this.opData[randomIndex];
    },

    clearUl(){
      this.filteredOps = [];
    },

    computeGuessButtonClass(){
      return this.solved ? "guess-button-active" : "guess-button-disabled"
    },

    resetGame(){
      this.genTargetOp();
      this.opGuesses = [];
      this.solved = false;
      this.filteredOps = [];
      this.searchText = "";
      this.numGuessesRemaining = 6;
    }
  }
};
</script>

<style scoped>
.list-head {
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center;
}

.list-head button {
  font-size: 2em;
  margin-left: 5px;
  background-color: gray;
  color:white;
  border-radius: 10px;
}

.list-head input {
  width: 35%;
  font-size: 2em;
  border-radius: 10px;
}

.list-body li {
  color: white;
  list-style-type: none;
  font-size: 2em;
  display: flex;
}

.list-body li:hover {
  background-color: darkslategray;
}

.list-body ul {
  background-color: gray;
  width: 33%;
  height: 400px;
  position: absolute;
  top: 38%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: auto;
  z-index: 9999;
}

.op-image {
  width: 50px;
  height: auto;
}
</style>