<template>
    <div id="main-info-comp">
        <h2 v-if="solved" class="solved-h2">Congratulations! Op was: <h2 class="op-name-h2">{{ targetOp.Name }}</h2><button @click="resetGame">Play Again</button></h2>
        <h2 v-else-if="numGuessesRemaining > 0" class="guesses-remaining-h2">Guesses Remaining: {{ numGuessesRemaining }}</h2>
        <h2 v-else class="no-guesses-h2">Game over. Op was: <h2 class="op-name-h2">{{ targetOp.Name }}</h2><button @click="resetGame">Play Again</button></h2>
    </div>
</template>

<script>
    export default{
        props:['resetGame','numGuessesRemaining', 'targetOp', 'solved'],
    }
</script>

<style scoped>
    .guesses-remaining-h2{
        color:white;
    }

    .solved-h2{
        color:green;
    }

    .no-guesses-h2{
        color:red;
    }

    .op-name-h2{
        color:yellow;
    }

    button{
        color: white;
        background-color: gray;
        font-size: 1.5em;
        border-radius: 10px;
    }

</style>
