<template>
  <div @click="showSearchCompUL(false)" class="background-overlay">
    <div class="app-header">
      <img alt="R6W logo" src="./assets/logo.png">
      <h2>Consecutive Guesses: {{ highScore }}</h2>
    </div>
   <OperatorSearchComp :showSearchCompUL="showSearchCompUL" :incrementHS="incrementHS" :resetHS="resetHS" :showUL="showUL" />
  </div>
</template>

<script>
import OperatorSearchComp from './components/OperatorSearchComp.vue'

export default {
  name: 'App',
  data(){
    return{
      showUL: false,
      highScore:0
    }
  },

  components: {
    OperatorSearchComp
  },

  methods: {
    showSearchCompUL(value){
      this.showUL=value;
    },

    incrementHS(){
      this.highScore += 1;
    },

    resetHS(){
      this.highScore = 0;
    }
  }
}
</script>

