<template>
    <div id="main-answer-comp">
        <div v-for="op in opChoices" :key="op" class="answer-row">
            <div class="row-head">
                <img :src="op.Icon" class="op-image">
                <h2>{{ op.Name }}</h2>
            </div>
            <div class="answer-catagory" :style="{ backgroundColor: op.SideColor }">
                <p>Side</p>
                <p>{{op.Side}}</p>
            </div>
            <div class="answer-catagory" :style="{ backgroundColor: op.SpeedColor}">
                <p>Speed</p>
                <p>{{op.Speed}}</p>
            </div>
            <div class="answer-catagory" :style="{ backgroundColor: op.DiffColor}">
                <p>Difficulty</p>
                <p>{{op.Difficulty}}</p>
            </div>
            <div class="answer-catagory" :style="{ backgroundColor: op.PrimColor}">
                <p>Primaries</p>
                <p>{{op.Primaries}}</p>
            </div>
            <div class="answer-catagory" :style="{ backgroundColor: op.SecColor}">
                <p>Secondaries</p>
                <p>{{op.Secondaries}}</p>
            </div>
            <div class="answer-catagory" :style="{ backgroundColor: op.GadColor}">
                <p>Gadgets</p>
                <p>{{op.Gadgets}}</p>
            </div>

        </div>
    </div>
</template>

<script>
export default {
    props: ['opGuesses', 'targetOp'],
    data() {
        return {
            opChoices: []
        }
    },

    watch: {
        opGuesses: {
            handler(newVal) {
                //Clear the opChoices once the opGuesses list resets
                if(newVal.length == 0){
                    this.opChoices = [];
                    return;
                }

                let opGuess = newVal[newVal.length - 1];
                let list1 = [];
                let list2 = [];

                let opGuessSideColor = opGuess.Side == this.targetOp.Side ? "green" : "red";
                let opGuessSpeedColor = opGuess.Speed == this.targetOp.Speed ? "green" : "red";
                let opGuessDiffColor = opGuess.Difficulty == this.targetOp.Difficulty ? "green" : "red";

                list1 = opGuess.Primaries.split(";");
                list2 = this.targetOp.Primaries.split(";");
                let numPrimsInCommon = this.countDuplicates(list1, list2);
                let primString = `${numPrimsInCommon} in common`;
                let opGuessPrimColor = numPrimsInCommon == list2.length ? "green" : numPrimsInCommon > 0 ? "yellow" : "red";

                list1 = opGuess.Secondaries.split(";");
                list2 = this.targetOp.Secondaries.split(";");
                let numSecsInCommon = this.countDuplicates(list1, list2);
                let secString = `${numSecsInCommon} in common`;
                let opGuessSecColor = numSecsInCommon == list2.length ? "green" : numSecsInCommon > 0 ? "yellow" : "red";

                list1 = opGuess.Gadgets.split(";");
                list2 = this.targetOp.Gadgets.split(";");
                let numGadsInCommon = this.countDuplicates(list1, list2);
                let gadString = `${numGadsInCommon} in common`;
                let opGuessGadColor = numGadsInCommon == list2.length ? "green" : numGadsInCommon > 0 ? "yellow" : "red";

                this.opChoices.push(
                    {
                        "Name": opGuess.Name,
                        "Icon": opGuess.Icon,
                        "Side": opGuess.Side,
                        "SideColor": opGuessSideColor,
                        "Speed": opGuess.Speed,
                        "SpeedColor": opGuessSpeedColor,
                        "Difficulty": opGuess.Difficulty,
                        "DiffColor": opGuessDiffColor,
                        "Primaries": primString,
                        "PrimColor": opGuessPrimColor,
                        "Secondaries": secString,
                        "SecColor": opGuessSecColor,
                        "Gadgets": gadString,
                        "GadColor": opGuessGadColor

                    }
                );


            },
            deep: true
        }
    },

    methods: {
        countDuplicates(list1, list2) {
            let duplicates = 0;
            let set1 = new Set(list1);

            // Count occurrences of elements from list2 that are present in set1
            list2.forEach(item => {
                if (set1.has(item)) {
                    duplicates++;
                }
            });

            return duplicates;
        }
    },
}
</script>

<style scoped>
.row-head {
    display: flex;
    width: 15%;
}

.answer-row {
    display: flex;
    margin: 5px;
    background-color: gray;
    border-radius: 10px;
}

.answer-catagory {
    margin-left: 20px;
    background-color: red;
    width: 7%;
    border-radius: 10px;
}

.answer-catagory h1 {
    color: white;
}

.op-image {
    margin-top: 10px;
    width: 50px;
    height: 50px;
}

#main-answer-comp {
    margin: 20px;
}
</style>